import * as React from 'react'
import { Button, Layout, Toast } from '../auto-ui'

const Demo: React.FC<any> = () => {
  const onClick = () => {
    Toast('', () => {
      Toast('callback')
    })
  }

  const onClickByDuration = () => {
    Toast('', 5000, () => {
      Toast('callback')
    })
  }

  return (
    <Layout>
      <Layout.Body>
        <Button onClick={onClick}></Button>
        <br />
        <Button onClick={onClickByDuration}>5</Button>
      </Layout.Body>
    </Layout>
  )
}

export default Demo
